import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchData } from "../../redux/Actions/CommonActions";
import consolaGlobalInstance from "consola";
class Matches extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
  }
  componentDidMount() {
    this.props.OnDataRecevied();
  }
  componentWillReceiveProps(netxProps) {
    console.log("Data Next Props", netxProps.data.data);
    // if (netxProps.data.data.length > 0) {/
      this.setState({ data: netxProps.data.data });
    // }
  }
  render() {
    //   consolaGlobalInstance.lo
    // console.log("DATA DTA A", this.props.data.data);
    return (
      <div>
        <ul>
          {this.state.data.map((d, index) => (
            <li>{d.employee_name}</li>
          ))}
        </ul>
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    loading: state.data.loading,
    error: state.data.error,
    data: state.data.data
  };
};

const mapDispatchToProps = dispatch => {
  return {
    OnDataRecevied: () => dispatch(fetchData())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Matches);
